import React from 'react'
import { Link } from 'react-router-dom'

function getSigIdShort(vc) {
  const id = vc.id;
  // get everything after the last slash
  const sigIdShort = id.substring(id.lastIndexOf('/') + 1);
  return sigIdShort;
}

function getSigIdShorts(scvp) {
  return scvp['verifiableCredential'].map(getSigIdShort);
}

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      link: this.props.link,
      did: require('../resources/json/did.json'),
      scvp: require('../resources/json/scvp.json'),
      sigIdShorts: getSigIdShorts(require('../resources/json/scvp.json')),
    }
  }

  render() {
    const name = this.state.did.subject.displayName;
    const image = this.state.did.subject.displayImg;
    const type = this.state.did.subject.type;
    const link = this.state.link;
    const linkVC = this.state.scvp.verifiableCredential.find(vc => vc.id.substring(vc.id.lastIndexOf('/') + 1) === link) || undefined;


    const getServiceVCs = () => {
      const serviceVCs = [];
      for (const vc of this.state.scvp.verifiableCredential) {
        if (vc.credentialSubject.type === "identity") {
          serviceVCs.push(vc);
        }
      }
      return serviceVCs;
    }

    return (
      <div className="mx-auto max-w-7xl px-4 py-16 sm:px-6 lg:px-8">
        <div className="mx-auto grid max-w-2xl grid-cols-1 grid-rows-1 items-start gap-x-8 gap-y-8 lg:mx-0 lg:max-w-none ">
          {/* Invoice */}
          <div className="-mx-4 px-4 py-8 shadow-sm ring-1 ring-gray-900/5 sm:mx-0 sm:rounded-lg sm:px-8 sm:pb-14 lg:col-span-2 lg:row-span-2 lg:row-end-2 xl:px-16 xl:pb-20 xl:pt-16">
            <div className="px-4 sm:px-0">
              <div className="flex justify-between">
                <img className="h-12 w-12 rounded-full self-end" src={image} alt="" />
                <Link to={`/did-app/sigchain/${this.state.sigIdShorts[0]}`} className='flex items-center'>
                  <h5 className="text-sm font-medium leading-6 text-gray-900">Sigchain</h5>
                  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-4 h-4">
                    <path strokeLinecap="round" strokeLinejoin="round" d="M8.25 4.5l7.5 7.5-7.5 7.5" />
                  </svg>
                </Link>
              </div>
              <h3 className="text-base font-semibold leading-7 text-gray-900 mt-2">{name}</h3>
              <p className="mt-1 max-w-2xl text-sm leading-6 text-gray-500">{type === 'person' ? "Individual" : "Organization"}</p>
            </div>
            <div className="mt-6 border-t border-gray-100">
              <dl className="divide-y divide-gray-100">
                {/* <div className="bg-gray-50 px-4 py-6 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-3">
                  <dt className="text-sm font-medium leading-6 text-gray-900">Display name</dt>
                  <dd className="mt-1 text-sm leading-6 text-gray-700 sm:col-span-2 sm:mt-0">{name}</dd>
                </div> */}
                {getServiceVCs().map((vc, index) => {

                  const service = vc.credentialSubject.service;
                  const id = vc.id.substring(vc.id.lastIndexOf('/') + 1);

                  if (id === this.state.link) {
                    return (
                      <div className="bg-green-200 px-4 py-6 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-3">
                        <dt className="text-sm font-medium leading-6 text-gray-900">{service.name}</dt>
                        <a href={service.user} className="mt-1 text-sm leading-6 text-blue-600 dark:text-blue-500 hover:underline  sm:mt-0">{service.user}</a>
                        <a href={service.proof} className="mt-1 text-sm leading-6 text-blue-600 dark:text-blue-500 hover:underline  sm:mt-0">{service.proof}</a>
                      </div>
                    )
                  }

                  return (
                    <div className="bg-gray-50 px-4 py-6 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-3">
                      <dt className="text-sm font-medium leading-6 text-gray-900">{service.name}</dt>
                      <a href={service.user} className="mt-1 text-sm leading-6 text-blue-600 dark:text-blue-500 hover:underline  sm:mt-0">{service.user}</a>
                      <a href={service.proof} className="mt-1 text-sm leading-6 text-blue-600 dark:text-blue-500 hover:underline  sm:mt-0">{service.proof}</a>
                    </div>
                  )
                }
                )}
              </dl>
            </div>

            {link !== undefined ?
              linkVC.credentialSubject.type === 'identity' ?
                <div className="mx-auto grid max-w-2xl grid-cols-1 grid-rows-1 items-start gap-x-8 gap-y-8 lg:mx-0 lg:max-w-none bg-green-500 mt-10">
                  <h3 className="text-base font-semibold leading-7 text-gray-900">It is proven!</h3>
                </div> :
                <div className="mx-auto grid max-w-2xl grid-cols-1 grid-rows-1 items-start gap-x-8 gap-y-8 lg:mx-0 lg:max-w-none bg-red-500 mt-10">
                  <h3 className="text-base font-semibold leading-7 text-gray-900">It is not proven!</h3>
                </div>
              : <></>
            }

          </div>

        </div>


      </div>
    )
  }
}

export default Home;