import { Prism } from '@mantine/prism';
import React from 'react'
import { Link, useParams } from 'react-router-dom';

function getVCAtIndex(idx, svcp) {
  return svcp.verifiableCredential[idx];
}

function classNames(...classes) {
  return classes.filter(Boolean).join(' ')
}

function getVC(id, scvp) {
  return scvp.verifiableCredential.find(vc => vc.id.substring(vc.id.lastIndexOf('/') + 1) === id);
}

function downloadObjectAsJson(exportObj, exportName) {
  var dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(exportObj, null, 2));
  var downloadAnchorNode = document.createElement('a');
  downloadAnchorNode.setAttribute("href", dataStr);
  downloadAnchorNode.setAttribute("download", exportName + ".json");
  document.body.appendChild(downloadAnchorNode); // required for firefox
  downloadAnchorNode.click();
  downloadAnchorNode.remove();
}

function Sigchain() {


  const { id } = useParams();
  const scvp = require('../resources/json/scvp.json')

  const [selectedChainlinkId, setSelectedChainlinkId] = React.useState(-1);
  const [payload, setPayload] = React.useState(null);
  const [signedPayload, setSignedPayload] = React.useState(null);
  const [selectedIndex, setSelectedIndex] = React.useState(0);
  const [formattedPayload, setFormattedPayload] = React.useState(null);



  React.useEffect(() => {
    console.log("id: " + id);

    if (id === -1) {
      return;
    }

    setSelectedChainlinkId(id);
    console.log("selectedChainlinkId: " + selectedChainlinkId);
  }, [id])

  React.useEffect(() => {
    if (selectedChainlinkId === -1) {
      return;
    }

    const vc = getVC(selectedChainlinkId, scvp);
    setPayload(vc);

    const formattedPayload = JSON.stringify(vc, null, 2);
    setFormattedPayload(formattedPayload);

    const signedPayload = vc.proof.jws;
    setSignedPayload(signedPayload);

    const index = scvp.verifiableCredential.indexOf(vc) + 1;
    setSelectedIndex(index);

    console.log("selectedVC: " + JSON.stringify(payload, null, 2));
  }, [selectedChainlinkId, scvp, signedPayload, payload])






  const renderChainLink = (vc, index) => {
    const id = vc.id.substring(vc.id.lastIndexOf('/') + 1);
    const isSelected = id === selectedChainlinkId;

    const getVCText = (vc) => {
      const type = vc['credentialSubject']['type'];
      switch (type) {
        case 'eldest':
          return 'Initial link';
        case 'identity':
          const name = vc['credentialSubject']['service']['name'];
          return 'claimed ownership of ' + name + ' account';
        case 'key':
          return 'added a new key';
        case 'revoke':
          const revokedSeqnos = vc['credentialSubject']['linkNo'];
          return 'revoked chainlink' + (revokedSeqnos.length > 1 ? 's' : '') + ' ' + revokedSeqnos.join(', ');
        default:
          return 'Unhandled vc type: ' + type;
      }
    }

    const vcText = getVCText(vc).charAt(0).toUpperCase() + getVCText(vc).slice(1);

    const numVCs = scvp.verifiableCredential.length;

    return <li className="group relative flex gap-x-4 gap-y-10 ">
      <Link to={`/did-app/sigchain/${id}`} className={classNames(isSelected ? 'relative flex w-full h-16 items-center pr-4 rounded-md bg-yellow-500' : 'relative flex w-full h-16 items-center pr-4 rounded-md group-hover:bg-yellow-500')}>
        <div
          className={classNames(
            index === numVCs - 1 ?
              // Last element
              '-bottom-6 absolute left-0 top-0 flex w-6 justify-center h-6' :
              classNames(index === 0 ?
                // First element
                ' -bottom-6 absolute left-0 flex w-6 justify-center h-12' :
                // Middle element
                '-bottom-6 absolute left-0 top-0 flex w-6 justify-center')
          )}
        >
          <div className="w-px bg-gray-200" />
        </div>
        <div className={classNames(isSelected ? "relative flex h-6 w-6 flex-none items-center justify-center bg-yellow-500" : "relative flex h-6 w-6 flex-none items-center justify-center bg-white group-hover:bg-yellow-500")}>
          <div className="h-1.5 w-1.5 rounded-full bg-gray-100 ring-1 ring-gray-300" />
        </div>
        <p className="flex-auto py-0.5 text-xs leading-5 text-gray-500 text-left">
          <span className="font-base text-gray-400 pr-5">{index + 1}</span>
          <span className="font-medium text-gray-900">{vcText}</span>
        </p>
      </Link>
    </li>
  }

  return <div className="mx-auto max-w-7xl px-4 py-16 sm:px-6 lg:px-8">
    <div className="mx-auto grid max-w-2xl grid-cols-1 grid-rows-1 items-start gap-x-8 gap-y-8 lg:mx-0 lg:max-w-none lg:grid-cols-2">
      <div className="-mx-4 px-4 py-8 shadow-sm ring-1 ring-gray-900/5 sm:mx-0 sm:rounded-lg sm:px-8 sm:pb-14 lg:col-span-2 lg:row-span-2 lg:row-end-2 xl:px-16 xl:pb-20 xl:pt-16">
        <Link to={`/did-app`} className='flex items-center'>
          <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-4 h-4">
            <path strokeLinecap="round" strokeLinejoin="round" d="M15.75 19.5L8.25 12l7.5-7.5" />
          </svg>
          <h5 className="text-sm font-medium leading-6 text-gray-900">Home</h5>
        </Link>
        <h2 className="text-base font-semibold leading-6 text-gray-900 mt-4">Activity</h2>
        <dl className=" mt-6 space-y-6 divide-y divide-gray-100 border-t border-gray-200 text-sm leading-6">
          <div className="mx-auto grid max-w-2xl grid-cols-2 grid-rows-1 items-start gap-x-8 gap-y-8 lg:mx-0 lg:max-w-none " >
            {/* Sigchain Info */}
            <div className="relative mt-8 w-full text-left text-sm leading-6">
              <ul className="space-y-6">
                {scvp.verifiableCredential.map((vc, index) => {
                  return renderChainLink(vc, index);
                })}
              </ul>
            </div>
            {/* Chainlink Info */}
            <div className="relative mt-8 w-full text-left text-sm leading-6 ">
              <div className="flow-root">
                <table className="">
                  <tr>
                    <h2 className="px-6 py-4 align-top whitespace-nowrap text-base font-semibold ">Chainlink {selectedIndex}</h2>
                  </tr>
                  {/* <tr>
                      <td className="px-6 py-4 align-top whitespace-nowrap text-sm font-medium text-gray-900">Signed:</td>
                      <td className="px-6 py-4 align-top whitespace-nowrap text-sm font-semibold">9/26/23 9:08pm</td>
                    </tr> */}
                  <tr>
                    <td className="px-6 py-4 align-top whitespace-nowrap text-sm font-medium text-gray-900">Payload:</td>
                    <td className="px-6 py-4 resize align-top whitespace-nowrap text-sm text-gray-500" style={{ resize: "both" }}>
                      <Prism language="json" style={{ resize: "both" }} className="resize overflow-auto w-96 h-52" >{JSON.stringify(payload, null, 2)}</Prism>
                    </td>
                  </tr>
                  {signedPayload === null ? <></> :
                    <tr>
                      <td className="px-6 py-4 align-top whitespace-nowrap text-sm font-medium text-gray-900">Signed payload:</td>
                      <td className="px-6 py-4 resize align-top whitespace-nowrap text-sm text-gray-500" style={{ resize: "both" }}>
                        <Prism language="json" style={{ resize: "horizontal" }} className='resize overflow-auto w-96' >{signedPayload}</Prism>
                      </td>
                    </tr>
                  }
                  <tr>
                    <td className="px-6 py-4 align-top whitespace-nowrap text-sm font-medium text-gray-900">Full JSON:</td>
                    <td className="px-6 py-4 align-top whitespace-nowrap text-sm text-gray-500">
                      <button onClick={() => (downloadObjectAsJson(payload, `chainlink${selectedIndex}`))} className="text-indigo-600 hover:text-indigo-900">Download</button>
                    </td>
                  </tr>
                </table>
              </div>
            </div>
          </div>
        </dl>
      </div>
    </div>
  </div>
}


export default Sigchain;





