import Home from './components/Home'
import { Route, Routes, Link } from "react-router-dom";
import Sigchain from './components/Sigchain';
import React from 'react';

function getSigIdShort(vc) {
  const id = vc.id;
  // get everything after the last slash
  const sigIdShort = id.substring(id.lastIndexOf('/') + 1);
  return sigIdShort;
}

function getSigIdShorts(scvp) {
  return scvp['verifiableCredential'].map(getSigIdShort);
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      scvp: require('./resources/json/scvp.json'),
      did: require('./resources/json/did.json'),
      sigIdShorts: getSigIdShorts(require('./resources/json/scvp.json')),
    };
  }

  render() {
    return (
      <Routes>
        <Route path="/did-app/" element={<Home />} />
        <Route path="/did-app/sigchain/:id" element={<Sigchain />} />
        {this.state.sigIdShorts.map((sigIdShort, sigIdShortIdx) => (
          <Route key={sigIdShortIdx} path={"/did-app/proof/" + sigIdShort} element={<Home link={sigIdShort} />} />
        ))}
      </Routes>
    )
  }
}

export default App;